    See the JupyterLab Developer Guide for useful CSS Patterns:

    https://jupyterlab.readthedocs.io/en/stable/developer/css.html
*/
:root {
  --as-main-bg-color-primary: var(--jp-layout-color0);
  --as-main-bg-color-secondary: var(--jp-layout-color1);
  --as-main-bg-color-third: var(--jp-layout-color2);
  --as-main-font-color-black: var(--jp-ui-font-color0);
  --as-main-font-color: var(--jp-ui-font-color1);
  --as-main-font-color-secondary: var(--jp-ui-font-color2);
  --as-main-disabled-color: var(--jp-ui-font-color3);
  --as-text-color-hover: var(--jp-input-hover-background);
  --as-background-hover: var(--jp-input-background);
  --as-border-color: var(--jp-border-color);
  --as-border-color-secondary: var(--jp-border-color1);
  --as-main-font-color-inverse: var(--jp-ui-inverse-font-color0);
  --as-main-font-color-gray-inverse: var(--jp-ui-inverse-font-color3);
  --as-main-bg-color-primary-inverse: var(--jp-inverse-layout-color0);
  --as-main-bg-color-gray-inverse: var(--jp-inverse-layout-color4);
  --as-border-color-light: var(--jp-border-color2);
}

/* @TODO remove this in the future */
.anaconda-assistant-tooltip {
  background-color: var(--as-main-bg-color-primary-inverse) !important;
  color: var(--as-main-font-color-inverse) !important;
}

.as-notebook-container #jp-right-stack,
.as-notebook-container #jp-left-stack {
  min-width: 480px !important;
}

.as-widget-container {
  min-width: 480px !important;
  background: #f5f5f5;
  height: 100%;
}

.as-widget-content {
  height: 100%;
  background: #fff;
  color: #000;
}

.as-container {
  background: var(--as-main-bg-color-secondary);
  color: var(--as-main-font-color);
  display: flex;
  flex-direction: column;
  font-family: 'Inter', sans-serif;
  height: 100%;
  overflow: auto;
  width: 100%;
}

.as-login-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 50px 50px;
  background: var(--as-main-bg-color-secondary);
  color: var(--as-main-font-color);
  height: 100%;
  font-family: 'Inter', sans-serif;
}

.as-login-container a {
  color: #6c5af6;
  text-decoration: underline;
  cursor: pointer;
}

.as-login-header {
  display: flex;
  margin-bottom: 40px;
  color: var(--as-main-font-color);
}

.as-login-body,
.as-login-footer {
  margin-left: 15px;
}

.as-login-body {
  flex-grow: 1;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
}

.as-login-body ul {
  margin: 20px 0;
  padding: 0;
  list-style: none;
}

.as-login-body ul li {
  display: flex;
  margin-bottom: 15px;
  align-items: center;
}

.as-login-body ul li * {
  fill: #0cca4a;
}

.as-login-body ul li.coming-soon {
  color: #6c5af6;
  margin-top: 35px;
}

.as-login-body ul li.disabled,
.as-login-body ul li.disabled * {
  color: var(--as-main-disabled-color);
  fill: var(--as-main-disabled-color);
}

.as-create-account-button {
  margin-bottom: 20px;
  margin-top: 20px;
  border-radius: 4px;
  padding: 10px 16px;
  background: #0cca4a;
  border: none;
  color: #06262d;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}

.as-create-account-button:hover {
  background: #4fab4f;
}

.as-login-sign-in-text {
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
}

.as-login-footer {
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  display: flex;
  margin-bottom: 60px;
}

.as-login-footer svg,
.as-login-body svg {
  width: 17px;
  height: 17px;
  margin-right: 10px;
}

.as-login-footer svg {
  width: 20px;
  height: 20px;
}
.as-login-icon {
  min-width: 30px;
  max-width: 30px;
  color: #31a824;
}

.as-title-text {
  font-size: 24px;
  font-weight: 700;
  margin-left: 10px;
  font-family: 'Inter', sans-serif;
}

.as-subtitle-text {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.as-home-screen-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.as-home-screen-icon {
  width: 80px;
  color: #31a824;
}

.as-home-screen-df-icon-container {
  width: 26px;
  height: 26px;
  background: #e9e6fe;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}

.as-home-screen-df-icon {
  width: 12px;
  height: 12px;
  color: #6c5af6;
}

.as-version-text {
  color: #bdbdbd;
  font-size: 11px;
  margin-top: 4px;
  margin-left: 10px;
}

.as-no-dataframe-container {
  display: flex;
  flex-direction: column;
  background: #f7f7f7;
  border-radius: 4px;
  padding: 20px 10px;
}

.as-no-dataframe-heading {
  font-size: 14px;
  font-weight: lighter;
  margin-bottom: 20px;
}

.as-no-dataframe-text {
  font-size: 12px;
  margin-bottom: 10px;
}

.as-no-dataframe-link {
  display: inline;
  color: blue;
  text-decoration: underline;
}

.as-no-dataframe-link:hover {
  color: darkblue;
  text-decoration: underline;
}

/*Main Chat Screen*/

.as-chat-main-section {
  overflow-y: scroll;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 16px;
}

.as-chat-message-container {
  display: flex;
  flex-direction: column;
}

.anaconda-assistant-export-chat {
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  padding: 0 20px;
  min-height: 26px;
  justify-content: center;
  color: #6c5af6;
  gap: 10px;
  cursor: pointer;
  margin-top: auto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
}

.anaconda-assistant-export-chat svg {
  height: 12px;
}

@keyframes ellipsis {
  to {
    width: 1.25em;
  }
}

@-webkit-keyframes ellipsis {
  to {
    width: 1.25em;
  }
}

.as-chat-footer-container {
  border-top: 3px solid #ced2d9;
  background: var(--as-background-hover);
  padding: 16px 10px;
  z-index: 9;
}

.as-chat-footer-container .anaconda-assistant-chat-input {
  margin: 0 6px 8px 6px;
}

.as-chat-footer-container .anaconda-assistant-chat-footer {
  margin: 0 18px;
}

#as-arrow,
#as-arrow::before {
  position: absolute;
  width: 8px;
  height: 8px;
  background: #caecca;
}

#as-arrow {
  visibility: hidden;
}

#as-arrow::before {
  visibility: visible;
  background: #caecca;
  content: '';
  transform: rotate(45deg);
  bottom: 4px;
}

.as-settings-container {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  margin-bottom: 40px;
  overflow-y: scroll;
}

.as-setting-control-container {
  border-bottom: 1px solid rgba(85, 85, 85, 0.05);
  padding: 20px;
  height: -webkit-fill-available;
  overflow-y: scroll;
}

.as-setting-control-title {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 10px;
}

.as-setting-control-inner-container {
  display: flex;
  align-items: center;
  justify-content: end;
  gap: 20px;
  font-size: 13px;
  margin: 20px 0;
}

.as-setting-control-description {
  max-width: 77%;
}

.as-settings-template-container {
  padding-top: 20px;
}

.as-settings-sub-header-container {
  font-size: 12px;
  font-weight: lighter;
  color: #9ca3af;
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
}

.as-setting-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.as-setting-control-description.as-setting-control-description-context-limit {
  max-width: 100%;
  flex-grow: 1;
}

.as-setting-control-description.as-setting-control-description-context-limit svg {
  width: 12px;
  margin-right: 4px;
  display: inline-block;
}

.as-setting-control-description.as-setting-control-description-context-limit
  .as-code-upgrade-button {
  align-items: center;
  background: #4fab4f;
  border-radius: 4px;
  border: none;
  color: white;
  cursor: pointer;
  display: flex;
  font-size: 16px;
  font-style: normal;
  justify-content: center;
  margin-bottom: 5px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: 20px;
  padding: 8px;
}

.as-setting-control-description.as-setting-control-description-context-limit
  .as-code-upgrade-button:hover {
  background: #0cca4a;
  color: #06262d;
}

.as-setting-control-description.as-setting-logout {
  max-width: 100%;
}

.as-setting-control-description .as-logout-button {
  color: #444;
  align-items: center;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  display: flex;
  font-size: 16px;
  font-style: normal;
  justify-content: center;
  padding: 8px;
}

.as-setting-control-description.as-setting-control-description-context-limit a {
  color: #6c5af6;
  text-decoration: underline;
}

.as-access-inner-container {
  padding: 0px 20px 0px 20px;
}

.as-access-inner-container-buttons {
  padding: 0px 20px 0px 20px;
  display: flex;
  justify-content: space-between;
}

.as-access-header {
  font-size: 24px;
  font-weight: lighter;
  margin-bottom: 10px;
  margin-top: 10px;
  font-family: 'Inter', sans-serif;
}

.as-access-text {
  font-size: 13px;
  font-weight: normal;
}

.as-access-inner-container a {
  color: #6c5af6;
  text-decoration: underline;
}

.as-terms-button,
.as-access-button,
.as-access-inner-container-buttons .as-logout-button {
  margin-top: 35px;
  border-radius: 4px;
  padding: 12px;
  background: #4fab4f;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 16px;
}

.as-terms-button {
  margin-left: auto;
}

.as-terms-button:hover {
  background: #0cca4a;
  color: black;
}

.as-terms-error-button {
  border-radius: 4px;
  padding: 12px;
  background: #3874cb;
  border: none;
  color: white;
  cursor: pointer;
}

.as-terms-error {
  border-radius: 6px;
  /* Transparent red */
  background-color: rgba(255, 0, 0, 0.1);
  padding: 10px;
  font-size: 12px;
}

.as-terms-error-text {
  color: #ff0000;
  font-size: 12px;
  margin-bottom: 10px;
}

.as-back-button-container {
  height: 30px;
  padding: 0 18px;
  display: flex;
  color: #6c5af6;
  margin-bottom: 10px;
}

.as-back-button {
  padding-top: 4px;
  padding-left: 2px;
  padding-right: 2px;
  color: #6c5af6;
  cursor: pointer;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;

  div {
    display: flex;
    align-items: center;

    svg {
      margin-right: 8px;
    }

    &.as-back-button-title {
      display: block;
      font-size: 12px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      min-width: 0;
    }
  }
}

.as-popup-outer-container {
  position: fixed;
  width: 480px;
  right: 32px;
  bottom: 80px;
}

.as-explain-popup {
  position: absolute;
  border: 1px solid rgba(224, 224, 224, 0.8);
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  background: white;
  z-index: 1;
}

.as-popup-container {
  padding: 0 16px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.as-popup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 13px;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #212121;
}

.as-popup-logo {
  display: flex;
  align-items: center;
  color: #2c303a;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
}

.as-popup-icon-container {
  background: #4fab4f;
  border-radius: 4px;
  margin-right: 10px;
  padding: 8px 4px 5px 4px;
}

.as-popup-icon {
  width: 20px;
  height: 16px;
  color: white;
}

.as-popup-close-icon {
  width: 14px;
  color: rgb(33, 33, 33, 0.5);
  cursor: pointer;
}

.as-popup-close-icon:hover {
  color: rgb(33, 33, 33, 0.8);
}

.as-popup-buttons {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding-bottom: 15px;
}

.as-popup-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  border: 1px solid #e6e6e6;
  margin-bottom: 3px;
  border-radius: 8px;
  padding: 9px 12px 9px 12px;
  cursor: pointer;
}

.as-popup-button:hover {
  background: rgba(33, 33, 33, 0.05);
}

.as-popup-button-icon {
  width: 8px;
  height: 12px;
  color: rgba(33, 33, 33, 0.5);
}

.as-popup-buttons-section {
  display: flex;
  align-items: center;
  font-size: 13px;
  font-weight: 590;
  letter-spacing: 0.02em;
}

/* Override box sizing rules */
.as-container *,
.as-container *::before,
.as-container *::after {
  box-sizing: inherit;
}

/* Override margin reset */
.as-container body,
.as-container h1,
.as-container h2,
.as-container h3,
.as-container h4,
.as-container h5,
.as-container h6,
.as-container p,
.as-container figure,
.as-container blockquote,
.as-container dl,
.as-container dd {
  margin-top: 14px;
  margin-bottom: 14px;
}

/* Override list style reset */
.as-container ul[role='list'],
.as-container ol[role='list'] {
  list-style: initial; /* or whatever default you prefer */
}

/* Override core root defaults */
.as-container html[focus-within],
.as-container html:focus-within {
  scroll-behavior: initial;
}

/* Override core body defaults */
.as-container body {
  min-height: initial;
  line-height: normal;
}

/* Override anchor styles */
.as-container a:not([class]) {
  text-decoration-skip-ink: initial;
}

/* Override image styles */
.as-container img,
.as-container svg,
.as-container picture {
  max-width: initial; /* or initial */
  display: inline-block;
}

.as-menu-item-label-message {
  opacity: 0.5;
  max-width: 260px;
  text-wrap: initial;
  line-height: 115%;
  padding-block: 8px;
  font-size: 11px;
}

.as-in-cell-menu {
  opacity: 0;
  animation: fadeIn 0.1s 0s forwards;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

.as-in-cell-menu-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}

.lm-Menu-content [data-command='assistant:context-header'] {
  font-weight: bold;
  color: inherit;
}

.lm-Menu-content [data-command='assistant:context-header'] .lm-Menu-itemIcon {
  opacity: 1;
}
