EACE,UAAU;AACZ","sourcesContent":["/*\n    See the JupyterLab Developer Guide for useful CSS Patterns:\n\n    https://jupyterlab.readthedocs.io/en/stable/developer/css.html\n*/\n:root {\n  --as-main-bg-color-primary: var(--jp-layout-color0);\n  --as-main-bg-color-secondary: var(--jp-layout-color1);\n  --as-main-bg-color-third: var(--jp-layout-color2);\n  --as-main-font-color-black: var(--jp-ui-font-color0);\n  --as-main-font-color: var(--jp-ui-font-color1);\n  --as-main-font-color-secondary: var(--jp-ui-font-color2);\n  --as-main-disabled-color: var(--jp-ui-font-color3);\n  --as-text-color-hover: var(--jp-input-hover-background);\n  --as-background-hover: var(--jp-input-background);\n  --as-border-color: var(--jp-border-color);\n  --as-border-color-secondary: var(--jp-border-color1);\n  --as-main-font-color-inverse: var(--jp-ui-inverse-font-color0);\n  --as-main-font-color-gray-inverse: var(--jp-ui-inverse-font-color3);\n  --as-main-bg-color-primary-inverse: var(--jp-inverse-layout-color0);\n  --as-main-bg-color-gray-inverse: var(--jp-inverse-layout-color4);\n  --as-border-color-light: var(--jp-border-color2);\n}\n\n/* @TODO remove this in the future */\n.anaconda-assistant-tooltip {\n  background-color: var(--as-main-bg-color-primary-inverse) !important;\n  color: var(--as-main-font-color-inverse) !important;\n}\n\n.as-notebook-container #jp-right-stack,\n.as-notebook-container #jp-left-stack {\n  min-width: 480px !important;\n}\n\n.as-widget-container {\n  min-width: 480px !important;\n  background: #f5f5f5;\n  height: 100%;\n}\n\n.as-widget-content {\n  height: 100%;\n  background: #fff;\n  color: #000;\n}\n\n.as-container {\n  background: var(--as-main-bg-color-secondary);\n  color: var(--as-main-font-color);\n  display: flex;\n  flex-direction: column;\n  font-family: 'Inter', sans-serif;\n  height: 100%;\n  overflow: auto;\n  width: 100%;\n}\n\n.as-login-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  margin: 50px 50px;\n  background: var(--as-main-bg-color-secondary);\n  color: var(--as-main-font-color);\n  height: 100%;\n  font-family: 'Inter', sans-serif;\n}\n\n.as-login-container a {\n  color: #6c5af6;\n  text-decoration: underline;\n  cursor: pointer;\n}\n\n.as-login-header {\n  display: flex;\n  margin-bottom: 40px;\n  color: var(--as-main-font-color);\n}\n\n.as-login-body,\n.as-login-footer {\n  margin-left: 15px;\n}\n\n.as-login-body {\n  flex-grow: 1;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 24px;\n}\n\n.as-login-body ul {\n  margin: 20px 0;\n  padding: 0;\n  list-style: none;\n}\n\n.as-login-body ul li {\n  display: flex;\n  margin-bottom: 15px;\n  align-items: center;\n}\n\n.as-login-body ul li * {\n  fill: #0cca4a;\n}\n\n.as-login-body ul li.coming-soon {\n  color: #6c5af6;\n  margin-top: 35px;\n}\n\n.as-login-body ul li.disabled,\n.as-login-body ul li.disabled * {\n  color: var(--as-main-disabled-color);\n  fill: var(--as-main-disabled-color);\n}\n\n.as-create-account-button {\n  margin-bottom: 20px;\n  margin-top: 20px;\n  border-radius: 4px;\n  padding: 10px 16px;\n  background: #0cca4a;\n  border: none;\n  color: #06262d;\n  cursor: pointer;\n  font-size: 16px;\n  font-weight: 700;\n  line-height: 24px;\n}\n\n.as-create-account-button:hover {\n  background: #4fab4f;\n}\n\n.as-login-sign-in-text {\n  font-size: 14px;\n  font-weight: 400;\n  line-height: 17px;\n}\n\n.as-login-footer {\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 20px;\n  display: flex;\n  margin-bottom: 60px;\n}\n\n.as-login-footer svg,\n.as-login-body svg {\n  width: 17px;\n  height: 17px;\n  margin-right: 10px;\n}\n\n.as-login-footer svg {\n  width: 20px;\n  height: 20px;\n}\n.as-login-icon {\n  min-width: 30px;\n  max-width: 30px;\n  color: #31a824;\n}\n\n.as-title-text {\n  font-size: 24px;\n  font-weight: 700;\n  margin-left: 10px;\n  font-family: 'Inter', sans-serif;\n}\n\n.as-subtitle-text {\n  font-size: 16px;\n  font-weight: 400;\n  line-height: 24px;\n}\n\n.as-hom